@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.square {
    /* center the cell content */
    justify-content: center;
    align-items: center;
    display: flex;
    font-family: Arial;
    font-size: 3rem;
    font-weight: bold;
    border: 1px solid #C9C9C9;
}
.game {
    display: inline-block;
}

.game .map {
    display: grid;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: #C9C9C9;
    border: 5px solid #C9C9C9;
    grid-template-columns: repeat(32, 15px);
    grid-template-rows: repeat(32, 15px);
}

.game .controls {
    width: 100%;
    margin: 0px ;
    background-color: #FAFAFA;
    font-family: 'Heebo', sans-serif;
}

.game .controls .MuiIconButton-root {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 6px;
}

.game .play-slider {
    float: right !important;
    width: 40%;
}

.game .play-slider .title {
    text-align: right;
    color: #848484;
}

.game .play-slider .slider {
    display: flex;
    align-items: center;
}
.colour-selectors .selector {
    min-width: 48px;
    min-height: 48px;
    border-radius: 0px;
    margin: 0.5rem;
}

.instructions .MuiGrid-container {
    justify-content: center;
}

.instructions .MuiGrid-item {
    max-width: 20rem;
}

.instructions h1 {
    font-family: 'Heebo', sans-serif;
}

.instructions h3 {
    font-family: 'Heebo', sans-serif;
    -webkit-margin-after: 0.5rem;
            margin-block-end: 0.5rem;
}

.instructions p {
    font-family: 'Roboto', sans-serif;
    -webkit-margin-before: 0.5rem;
            margin-block-start: 0.5rem;
}

.instructions > * {
    margin-left: auto;
    margin-right: auto;
}
body {
    background: #FAFAFA;
}

.page {
    display: grid;
    grid-template-areas:
        "game   title"
        "game   info"
        "colour  info";

    grid-template-columns: 3fr 5fr;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 2rem;
}

.page > .title {
    width: 100%;
    grid-area: title;
}

.page > .colour-selectors {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    justify-self: center;
    grid-area: colour;
}

.page > .game {
    grid-area: game;
    justify-self: left;
}

.page > .instructions {
    width: 100%;
    grid-area: info;
}

@media (max-width:1275px){
    .page {
        grid-template-areas:
            "title"
            "colour"
            "game"
            "info";
        grid-template-columns: 1fr;
        justify-items: center;
    }

    .page > .game {
        justify-self: center;
    }

    .page > .colour-selectors {
        flex-direction: row;
    }
}


@media (max-width:750px){
    .page {
        margin: 0rem;
        grid-gap: 1rem;
        gap: 1rem;
    }
}


