@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
    background: #FAFAFA;
}

.page {
    display: grid;
    grid-template-areas:
        "game   title"
        "game   info"
        "colour  info";

    grid-template-columns: 3fr 5fr;
    gap: 2rem;
    margin: 2rem;
}

.page > .title {
    width: 100%;
    grid-area: title;
}

.page > .colour-selectors {
    width: fit-content;
    justify-self: center;
    grid-area: colour;
}

.page > .game {
    grid-area: game;
    justify-self: left;
}

.page > .instructions {
    width: 100%;
    grid-area: info;
}

@media (max-width:1275px){
    .page {
        grid-template-areas:
            "title"
            "colour"
            "game"
            "info";
        grid-template-columns: 1fr;
        justify-items: center;
    }

    .page > .game {
        justify-self: center;
    }

    .page > .colour-selectors {
        flex-direction: row;
    }
}


@media (max-width:750px){
    .page {
        margin: 0rem;
        gap: 1rem;
    }
}

